.footer {
  position: relative;
  z-index: 1000;
  height: 150px;
  background-color: #e60000;
  color: white;
}

.footer-logo {
  vertical-align: top;
  margin-top: 60px;
  margin-left: 60px;
  font-size: 32px;
  display: inline-block;
}

.top-shadow {
  width: 100%;
  overflow-y: hidden;
  padding: 0 0 10px;
}
.top-shadow > div {
  width: 100%;
  box-shadow: 0px 0px 6px 3px #676767;
  -moz-box-shadow: 0px 0px 6px 3px #676767;
  -webkit-box-shadow: 0px 0px 6px 3px #676767;
}

@media only screen and (max-width: 700px) {
  .footer-logo {
    margin-left: 30px;
  }
  .footer-text {
    margin-left: 30px;
  }
}
@media only screen and (max-width: 600px) {
  .footer {
    height: 230px;
  }
  .footer-logo {
    margin-left: 30px;
  }
  .footer-text {
    margin-right: 30px;
  }
}
@media only screen and (max-width: 400px) {
  .footer {
    height: 330px;
  }
}
