a {
  color: inherit;
  text-decoration: none;
}

.menu {
  position: fixed;
  height: 70px;
  top: 0;
  width: 100%;
  color: rgb(32, 32, 32);
  -webkit-box-shadow: 0px 8px 7px -5px #d7d7d7;
  -moz-box-shadow: 0px 8px 7px -5px #d7d7d7;
  box-shadow: 0px 8px 7px -5px #d7d7d7;
  -webkit-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  background-color: #ffffff;
  z-index: 100;
}

.menu > div:first-child {
  color: #e60000;
  font-size: 40px;
  margin: 10px 20px;
  display: inline-block;
}
.desktop-menu {
  float: right;
  margin-top: 25px;
  margin-right: 15px;
  display: inline-block;
}
.desktop-menu > div {
  margin-left: 30px;
  display: inline-block;
}
.desktop-menu > div:hover {
  color: #e60000;
}
.desktop-menu > div:last-child {
  margin-right: 25px;
}
.mobile-menu {
  float: right;
  margin-top: 13px;
  margin-right: 15px;
  display: inline-block;
}

@media only screen and (min-width: 801px) {
  .mobile-menu {
    display: none;
  }
}
@media only screen and (max-width: 800px) {
  .desktop-menu {
    display: none;
  }
  .menu > div:first-child {
    color: #e60000;
    font-size: 32px;
    margin: 18px 10px;
    display: inline-block;
  }
}
